@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Comfortaa:wght@300&display=swap);
body{font-family:'Balsamiq Sans', cursive}nav{padding:0;margin:0}#mainNav:hover+#extNav,#extNav:hover{width:400px;height:400px;border:1px solid black}#mainNav{position:absolute;cursor:pointer;width:200px;height:200px;background:#808e9b;background-blend-mode:multiply;border-radius:100%;align-content:center;vertical-align:middle;transform:translate(-45%, -45%);transition:.3s;box-shadow:0 0 2px 2px black;z-index:1}#mainNav img{position:absolute;width:100px;height:100px;right:10%;bottom:10%;transition:.3s}#about,#projects{display:flex;vertical-align:middle;align-content:center}#about i,#projects i{margin-right:5px}#extNav{position:relative;width:0;height:0;background-color:#b2bec3;border-radius:100%;top:0;left:0;transform:translate(-50%, -50%);transition:.5s;z-index:0}#extNav #projects{position:absolute;bottom:12%;right:20%}#extNav #about{position:absolute;top:60%;right:5%}#extNav div:hover{cursor:pointer;color:#3c40c6}

#moveBg{margin:0;position:absolute;width:100%;height:200vh;left:0;top:0;z-index:-2}#staticBg{position:fixed;bottom:0;left:0;height:100%;width:100%;z-index:-1;background-image:url(/CV/static/media/Pines&Mountains.6e918813.svg);background-position:bottom;background-repeat:no-repeat;background-size:100%}

.hideScrollBar{-ms-overflow-style:none;scrollbar-width:none}.hideScrollBar::-webkit-scrollbar{display:none}#aboutCurrentProject{position:absolute;top:10%;right:15%;width:400px;height:400px}#aboutProjectTechnologies{position:absolute;top:110%;left:15%;width:400px;height:400px}fieldset{position:relative;height:100%;width:100%;border:3px solid;border-image-slice:1;border-image-source:linear-gradient(to top, black, transparent 90%);padding:0;overflow:hidden}fieldset legend{height:30px;line-height:30px;width:100%;border-top:1px solid black;border-radius:80%;font-family:'Comfortaa', cursive;font-weight:800;font-size:1.2rem}fieldset .cardBg{position:relative;width:100%;height:100%;background:linear-gradient(to top, white, transparent)}fieldset .cardBg .textField{margin:auto;min-height:90%;min-width:90%;margin-top:20px;padding:10px;font-weight:500;line-height:2rem;overflow-x:hidden;overflow-y:scroll;white-space:pre-wrap}

